$ic-angular: 0px 0px 100px 100px;
$ic-babel: 0px -100px 100px 100px;
$ic-bootstrap: 0px -200px 100px 100px;
$ic-jasmine: 0px -300px 100px 100px;
$ic-jquery: 0px -400px 100px 100px;
$ic-mocha: 0px -500px 100px 100px;
$ic-react: 0px -600px 100px 100px;
$ic-redux: 0px -700px 100px 100px;

@mixin sprite-width($sprite) {
  width: nth($sprite, 3);
}

@mixin sprite-height($sprite) {
  height: nth($sprite, 4);
}

@function sprite-width($sprite) {
  @return nth($sprite, 3);
}

@function sprite-height($sprite) {
  @return nth($sprite, 4);
}

@mixin sprite-position($sprite) {
  $sprite-offset-x: nth($sprite, 1);
  $sprite-offset-y: nth($sprite, 2);
  background-position: $sprite-offset-x  $sprite-offset-y;
}

@mixin sprite($sprite) {
  @include sprite-position($sprite);
  background-repeat: no-repeat;
  overflow: hidden;
  display: inline-block;
  @include sprite-width($sprite);
  @include sprite-height($sprite);
}

.ic-angular {
  @include sprite($ic-angular);
  background-image: url('ics-skill-framework&library.png');
}

.ic-babel {
  @include sprite($ic-babel);
  background-image: url('ics-skill-framework&library.png');
