$ic-css3: 0px 0px 100px 100px;
$ic-html5: 0px -100px 100px 100px;
$ic-js: 0px -200px 100px 100px;
$ic-less: 0px -300px 100px 100px;
$ic-nodejs: 0px -400px 100px 100px;
$ic-sass: 0px -500px 100px 100px;
$ic-typescript: 0px -600px 100px 100px;

@mixin sprite-width($sprite) {
  width: nth($sprite, 3);
}

@mixin sprite-height($sprite) {
  height: nth($sprite, 4);
}

@function sprite-width($sprite) {
  @return nth($sprite, 3);
}

@function sprite-height($sprite) {
  @return nth($sprite, 4);
}

@mixin sprite-position($sprite) {
  $sprite-offset-x: nth($sprite, 1);
  $sprite-offset-y: nth($sprite, 2);
  background-position: $sprite-offset-x  $sprite-offset-y;
}

@mixin sprite($sprite) {
  @include sprite-position($sprite);
  background-repeat: no-repeat;
  overflow: hidden;
  display: inline-block;
  @include sprite-width($sprite);
  @include sprite-height($sprite);
}

.ic-css3 {
  @include sprite($ic-css3);
  background-image: url('ics-skill-language.png');
}

.ic-html5 {
  @include sprite($ic-html5);
  background-image: url('ics-skill-language.png');
}

.ic-js {
  @include sprite($ic-js);
  background-image: url('ics-skill-language.png');
}

.ic-less {
  @include sprite($ic-less);
  background-image: url('ics-skill-language.png');
}

.ic-nodejs {
  @include sprite($ic-nodejs);
  background-image: url('ics-skill-language.png');
}

.ic-sass {
  @include sprite($ic-sass);
  background-image: url('ics-skill-language.png');
}

.ic-typescript {
  @include sprite($ic-typescript);
  background-image: url('ics-skill-language.png');
}




