$ic-docker: 0px -100px 100px 100px;
$ic-git: 0px -200px 100px 100px;
$ic-grunt: 0px -300px 100px 100px;
$ic-gulp: 0px -400px 100px 100px;
$ic-karma: 0px -500px 100px 100px;
$ic-firebase: 0px -600px 100px 100px;
$ic-npm: 0px -700px 100px 100px;
$ic-protractor: 0px -800px 100px 100px;
$ic-svn: 0px -900px 100px 100px;
$ic-travis: 0px -1000px 100px 100px;
$ic-webpack: 0px -1100px 100px 100px;

@mixin sprite-width($sprite) {
  width: nth($sprite, 3);
}

@mixin sprite-height($sprite) {
  height: nth($sprite, 4);
}

@function sprite-width($sprite) {
  @return nth($sprite, 3);
}

@function sprite-height($sprite) {
  @return nth($sprite, 4);
}

@mixin sprite-position($sprite) {
  $sprite-offset-x: nth($sprite, 1);
  $sprite-offset-y: nth($sprite, 2);
  background-position: $sprite-offset-x  $sprite-offset-y;
}

@mixin sprite($sprite) {
  @include sprite-position($sprite);
  background-repeat: no-repeat;
  overflow: hidden;
  display: inline-block;
  @include sprite-width($sprite);
  @include sprite-height($sprite);
}

